<route>
{
    meta: {
        title: "Arco",
        parentTitle: "MeltUI",
        layout: "main",
        parentIcon: "config"
    }
}
</route>

<template>
    <div>
        <ArcoCrudTable
            class="mt-2"
            :data="data"
            :options="options"
            :schema="schema"
            @showItem="showItem"
        />
    </div>
</template>

<script setup>
import {
    ArcoCrudTable,
    FormSchema,
    CrudOptions
} from 'meltui'

import { Modal } from '@arco-design/web-vue'

const searchable = ref(true)
const editable = ref(false)
let options = ref(new CrudOptions()
    // .edit() // 
    // .header().visible(false) // 
    .row().hover().border().stripe()
    // .size().medium()
    .layout()
    .search().inline().cols().sm(1).md(2).xl(3)
    // .row().selection().radioType()
    .row().selection().checkboxType().checkAll() // .currentOnly(false)
    // .row().expand().width(50).title('').render("{{ record.key % 2 === 1 ? ' is' + record.name + ',  ' + record.address : JSON.stringify(record, null, 2)  }}")
    // @fix   --> v-model:selected-keys
    // .body().virtualList().height(300)
    // .body().scroll().x(1000)
    .column().resizable()
    .viewOperation().clickEmit('showItem')
    .editOperation()
    .removeOperation()
    .parse())

console.log(options.value)

watch(() => editable.value, current => {
    // console.log(current, prev)
    // console.log(new CrudOptions(options.value).edit(current).parse())
    options.value = new CrudOptions(options.value).edit(current).parse()
    // console.log("")
    // console.log(JSON.stringify(options.value))
}, {
    deep: true
})
watch(() => searchable.value, current => {
    // console.log(current, prev)
    // console.log(new CrudOptions(options.value).edit(current).parse())
    options.value = new CrudOptions(options.value).search(current).parse()
    // console.log("")
    // console.log(JSON.stringify(options.value))
}, {
    deep: true
})
const schema = ref({
    name: new FormSchema()
        .title().upperFirst()
        // .width(200)
        .left().format('{{ \'[No.\' + rowIndex  + \']\' + record.name }}')
        .readonly()
        .column().fixed().left()
        .input().placeholder('').clearable()
        .cell().ellipsis().tooltip().width(150) // width
        .searchable() // .placeholder("{{ '' + column.title }}")
        .parse(),
    salary: new FormSchema()
        .title('') // .width(150).center()
        .inputNumber().placeholder('').clearable(false)
        // .props({
        //     allowClear: true
        // })
        .sortable().asc().desc()
        .filterable()
        .gt(20000)
        .gt([25000, 100000])
        // .filter("{{ record.salary > value[0] }}")
        .searchable().advancedOnly() // .placeholder("{{ '' + column.title }}")
        .parse(),

    address: new FormSchema()
        .title('') // .width(250).center()
        .filterable()
        // .startsWith("").startsWith("35 Park Road")
        // .startsWith(["", "35 Park Road"])
        .includes(['', '', 'Park Road'])
        // .filter(`{{ record.address.startsWith(value[0]) }}`) // 
        .textArea().clearable().placeholder('{{ \'\' + record.name + \'\'}}')
        .props({
            autoSize: true
        })
        .searchable().advancedOnly() // .placeholder("{{ '' + column.title }}")
        .parse(),
    province: new FormSchema()
        .title('')
        // .width(150)
        .center()
        // .column().fixed().right()
        .select({
            province: ['', '', ''],
            // 'province': ['', '', ''],
            '': ['', '', ''],
            '': ['', ''],
            '': ['', '']
        })
        // .props({
        //     allowClear: true
        // })
        .parse(),
    city: new FormSchema()
        .title('')
        // .width(150)
        .center()
        // .column().fixed().right()
        .select().keepWatch('province') // 
        .parse()
})
// schema.value.name = new FormSchema(schema.value.name).title("").parse()
const data = reactive([
    {
        key: '1',
        name: 'Jane Doe',
        salary: 23000,
        address: '',
        province: '',
        city: '',
        // disabled: true,
        email: 'jane.doe@example.com'
    },
    {
        key: '2',
        name: 'Alisa Ross',
        salary: 25000,
        address: '35 Park Road, London',
        email: 'alisa.ross@example.com'
    },
    {
        key: '3',
        name: 'Kevin Sandra',
        salary: 22000,
        address: '',
        province: '',
        city: '',
        email: 'kevin.sandra@example.com'
    },
    {
        key: '4',
        name: 'Ed Hellen',
        salary: 17000,
        address: '42 Park Road, London',
        email: 'ed.hellen@example.com'
    },
    {
        key: '5',
        name: 'William Smith',
        salary: 27000,
        address: '62 Park Road, London',
        email: 'william.smith@example.com'
    }
])
for (let i = 10; i < 1000; i++) {
    data.push({
        key: i + 'xx',
        name: 'William Smith',
        salary: 27000,
        address: '62 Park Road, London',
        email: 'william.smith@example.com'
    })
}
const showItem = argv => {
    const { record } = argv
    // getCurrentInstance()?.appContext.config.globalProperties.$modal.info({ title:'Name', content: record.name })
    Modal.info({
        title: 'Info Title',
        content: JSON.stringify(record, null, 2)
    })
}

</script>
